<script type="text/javascript">
	import Header from '../commons/Header.vue'
	export default {
		name:'Download',
		components:{
			Header
		},
		data(){
			return {
				show:false
			}
		},
		methods:{
			showtime(){
				setTimeout(()=>{
					this.show=true
				},100)
			}
		},
		mounted(){
			this.showtime()
		}
	}
</script>
